// header
.hero-image {
  background: no-repeat center url("../images/hero_1.jpg");
  background-size: cover;
  height: 100vh;
  position: relative;

}

.hero-image::before {
  content: "";
  background-color: #000;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: .4;
}

.hero-content {
  color: #fff;
  text-align: center;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;

  .hero-title {
    font-size: 3.5rem;
  }

  .hero-subtitle {
    font-size: 1.875rem;
    font-weight: 400;
    line-height: 1.75;
  }
}



.content {
  display: none;
	text-align: center;
}
.modal {
  display: none;
  height: 100%;
	width: 100%;
	position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
}
.modal .overlay {
  background-color: #000;
  height: 100%;
	width: 100%;
	position: relative;
  opacity: 0.5;
  z-index: 100;
}
.modal .message {
	-webkit-box-shadow: 0 0 25px -5px #000;
	        box-shadow: 0 0 25px -5px #000;
	border-radius: 10px ;
  background: #f3f3f3;
	height: 200px;
	width: 320px;
	margin: auto;
  padding: 10px;
	text-align: center;
	position: absolute;
	top: 0;
	left: 0;
  bottom: 0;
	right: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
  z-index: 101;
}
.success-error {
	display: none;
}
select {
	color: #fff;
	background-color: #777;
	width: 120px;
	font-size: larger;
}
.formsubmit {
  background-color: #ededed;
	color: #777;
	cursor: pointer;
  border: 1px solid #dcdcdc;
  display: inline-block;
  font-family: arial;
  font-size: 14px;
  font-weight: bold;
  height: 25px;
	width: 75px;
	margin: .75rem 0;
  line-height: 25px;
  text-align: center;
	-webkit-transition: .3s;
	transition: .3s;
}
.formsubmit:hover {
  background-color:#dfdfdf;
}
